// sider
.layout-sider-wrapper {
  position: relative;
  background: #264653;
  .ant-layout-sider-trigger {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 40px;
    background: #023047;
    .anticon,
    .anticon svg {
      display: block;
      width: 30px;
      height: 30px; } }
  .layout-sider-logo {
    padding: 8px 15px;
    margin: 0;
    > a {
      display: block;
      position: relative;
      height: 48px;
      font-weight: bold;
      color: #2a9d8f;
      background: #ffffff;
      span {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0; }
      .layout-sider-logo-f {
        line-height: 20px;
        font-size: 1.25rem;
        padding: 4px 8px;
        opacity: 1;
        transform: scale(1);
        transition: opacity 0.3s, transform 0s; }
      .layout-sider-logo-s {
        opacity: 0;
        line-height: 48px;
        font-size: 2rem;
        text-align: center;
        transform: scale(0);
        transition: opacity 0.3s, transform 0s 0.3s; } } }
  &.ant-layout-sider-collapsed {
    .layout-sider-logo {
      > a {
        .layout-sider-logo-f {
          opacity: 0;
          transform: scale(0);
          transition: opacity 0.3s, transform 0s 0.3s; }
        .layout-sider-logo-s {
          opacity: 1;
          transform: scale(1);
          transition: opacity 0.3s, transform 0s; } } } }
  .layout-sider-menu {
    border-right: none;
    background: transparent;
    .ant-menu-item {
      display: flex;
      align-items: center;
      width: 100%;
      border-radius: 20px 0 0 20px;
      background: transparent;
      margin: 0 0 4px;
      padding: 4px 15px;
      .anticon,
      .anticon svg {
        displkay: block;
        width: 28px;
        height: 28px; }
      .anticon {
        margin-rgiht: 4px;
        + span {
          display: block;
          line-height: 1.5;
          font-size: 1rem;
          color: #ffffff; } }
      .anticon svg {
        fill: #ffffff; }
      &.ant-menu-item-selected {
        background: #2a9d8f; } }
    .ant-menu-submenu {
      .ant-menu-submenu-title {
        display: flex;
        align-items: center;
        width: 100%;
        border-radius: 20px 0 0 20px;
        background: transparent;
        margin: 0 0 4px;
        padding: 4px 25px 4px 15px;
        .ant-menu-submenu-arrow {
          display: block;
          width: 0;
          height: 0;
          border-top: 3px solid #ffffff;
          border-right: 3px solid #ffffff;
          border-left: 3px solid transparent;
          border-bottom: 3px solid transparent;
          right: 15px;
          margin-top: -3px;
          transform: rotate(45deg);
          &::before,
          &::after {
            display: none; } }
        .anticon,
        .anticon svg {
          displkay: block;
          width: 28px;
          height: 28px; }
        .anticon {
          margin-rgiht: 4px;
          + span {
            display: block;
            line-height: 1.5;
            font-size: 1rem;
            color: #ffffff; } }
        .anticon svg {
          fill: #ffffff; } }
      &.ant-menu-submenu-selected {
        .ant-menu-submenu-title {
          background: #2a9d8f; } } } } }
.layout-sider-popup {
  .ant-menu {
    min-width: 140px;
    .ant-menu-item {
      height: auto;
      line-height: 1.5;
      font-size: 0.875rem;
      color: #222222;
      margin: 0 !important;
      padding: 8px 10px;
      &:hover {
        color: #222222;
        background: rgba(#2a9d8f, 0.1); }
      &.ant-menu-item-selected {
        color: #ffffff;
        background: #2a9d8f; } } } }

